.notice {
  width: 100%;
  padding-top: 0.3rem;
  // overflow: auto;
  .help_top {
    width: 80%;
    height: 2.34rem;
    box-shadow: 0 0 0.2rem 0.01rem rgba(105, 106, 230, 0.05);
    background: rgba(255, 255, 255, 0.9);
    border-radius: 0.12rem;
    position: relative;
    .bg_right {
      width: 50%;
      height: 67%;
      background: url(../../../assets/images/bg-notice.png);
      background-repeat: no-repeat;
      background-size: 100% 100%;
      position: absolute;
      right: 0;
      bottom: 0.2rem;
    }
    .help_title {
      font-size: 0.22rem;
      font-weight: bold;
      line-height: 0.28rem;
      color: #040e34;
      margin-top: 0.4rem;
      margin-left: 0.6rem;
    }
    .help_searh {
      width: 4rem;
      height: 0.38rem;
      line-height: 0.38rem;
      border: 0.01rem solid #d9dbe1;
      margin: 0.3rem 0;
      margin-left: 0.6rem;
      padding-left: 0.1rem;
      img {
        width: 0.2rem;
        height: 0.2rem;
      }
    }
    .help_tabs {
      padding-top: 0.2rem;
      .ant-tabs {
        color: #9696a3;
      }
      .ant-tabs-nav-list {
        margin-left: 0.6rem;
        .ant-tabs-tab {
          padding: 0.12rem 0;
          font-size: 0.14rem;
        }
      }
      .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
        color: #5c5ee4;
      }
      .ant-tabs-nav::before {
        border: none;
      }
      .ant-tabs-tab-active:after {
        content: "";
        height: 2px;
        width: 10px;
        background-color: #5c5ee4;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
      }
      .ant-tabs-ink-bar {
        display: none;
      }
      .ant-tabs-nav {
        margin: 0;
      }
      .ant-tabs-tab:hover {
        color: #9696a3;
      }
      .system {
        justify-content: space-between;
        margin-top: 0.4rem;
        padding: 0.4rem 15%;
        width: 100%;
        background-color: #fff;
        border-radius: 0.12rem;
        .detialtitle {
          font-size: 0.32rem;
          font-weight: bold;
          line-height: 0.5rem;
          color: #040e34;
          text-align: center;
        }
        .subtimebar {
          justify-content: space-evenly;
          // padding: 0 22%;
          .keywords {
            max-width: 50%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
        .attachment_box {
          margin-top: 0.2rem;
        }
        .download {
          color: #1890ff;
          cursor: pointer;
        }
        .download:hover {
          opacity: 0.7;
        }
        .system_con {
          margin-top: 0.2rem;
        }
        .content_footer {
          padding-top: 0.2rem;
          width: 100%;
          left: 0;
          .footer_page {
            cursor: pointer;
          }
          .footer_page:hover {
            color: #5c5ee4;
          }
        }
      }
      .station {
        margin-top: 0.4rem;
        padding: 0.25rem 0.6rem;
        width: 100%;
        background-color: #fff;
        border-radius: 0.12rem;
        .station_list {
          .station_item {
            color: #9696a3;
            margin: 0.15rem 0;
            cursor: pointer;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }

        .station_page {
          margin-top: 0.5rem;
          width: 100%;
          .ant-pagination {
            padding: 0 0;
          }
        }
      }
    }
  }
}

@primary-color: #5C5EE4;